<div class="person-component"><div class="person" v-if="!isDataProfileEditing"><div class="person__content"><base-avatar class="person__avatar"></base-avatar><div class="person__data"><div><h3>{{ personalData.name }}</h3></div><div><span>Почта:</span><span>{{ personalData.email }}</span></div><div><span>Телефон:</span><span>{{ personalData.phone_number }}</span></div></div></div><div class="person__settings"><base-button class="person__btn" customType="transparent" @handleClick="changePersonalData">Редактировать профиль</base-button></div></div><div class="person" v-else><div class="person__content"><label :class="['person__avatar_editing', { hovered: avatarIsHovered }]"><base-avatar class="person__avatar" @dragover.prevent="avatarIsHovered = true" @dragleave.prevent="avatarIsHovered = false" @dragstart.prevent @drop.prevent="setPhoto($event.dataTransfer.files[0])"></base-avatar><base-input class="person__file" type="file" @handleChange="setPhoto"></base-input></label><form class="person__form" @submit.prevent="setPersonData"><div class="person__inputs"><base-input class="person__input" placeholder="Имя" v-model="newPersonalData.name" :error="validation.firstError('newPersonalData.name')"></base-input><base-input class="person__input" placeholder="Телефон" v-model="newPersonalData.phone_number" :error="validation.firstError('newPersonalData.phone_number')"></base-input><base-input class="person__input" placeholder="Новый пароль" type="password" v-model="newPersonalData.password" :error="validation.firstError('newPersonalData.password')"></base-input></div><div class="person__buttons"><base-button class="person__btn person__btn_editing" customType="submit">Сохранить</base-button><base-button class="person__btn person__btn_editing" customType="fill" @handleClick="resetChanges()">Отмена</base-button></div></form></div></div></div>