<div class="project-item"><base-button class="project__remove" v-if="editable" customType="icon" name="trash" @click.prevent="removeExistedProject"></base-button><div class="project__preview"><base-icon class="project__icon" name="user"></base-icon></div><div class="project__file"><div class="project__title">{{ project.title }}</div><div class="project__name">{{ project.DBSheme }}</div><div class="project__last-active">{{ project.activity }}</div></div></div>