<div class="project-list" v-if="projects.length"><div class="projects__title"><span>Имя файла</span><span>Вид схемы БД</span><span>Последняя активность</span></div><ul class="project__menu" v-for="project in projects" :key="project.id"><li class="project__item"><div class="item__link" @click="openProject(project.id)"><project-list-item :project="project" :editable="editable"></project-list-item></div></li></ul></div><div class="project-list__not-exist" v-else>Сохраненные проекты не найдены</div>